import React, { Component } from 'react';

export class Device extends Component {
    constructor(props) {
        super(props);
        this.state = {
            img: '',
            searchText: '',
        }

        this.getImage = this.getImage.bind(this);
        this.storeSearchText = this.storeSearchText.bind(this);
    }

    storeSearchText(event) {
        this.setState({ searchText: event.target.value });
    }

    getImage() {
        let url = `https://tcksz3lkhc.execute-api.us-east-1.amazonaws.com/dev/img?imgName=${this.state.searchText}`;

        return (window.fetch(url, {
            method: "GET",
        }).then((response) => {
            return response.json();
        }).then((response) => {
            this.setState({
                img: response.imgURL
            });
            console.log(response)
        }).catch((err) => {
            console.log(err);
        }));
    }

    render() {
        return (
            <div className="text-center w-100">
                <div className="w-75 mt-3 mb-3 inline-block">
                    <h1 className="text-dark text-center mb-5">Device Camera View</h1>
                    <button className="btn btn-dark mr-3 ml-5" onClick={this.getImage}>Get image from device</button>
                    <input onChange={(e) => {
                        this.storeSearchText(e);
                    }} />
                    {
                        this.state.img
                            ? <img className="vegeImage" src={this.state.img} alt="camera view" />
                            : <p className="mt-3">Image name format: MM-DD-YYYY-Hour</p>
                    }
                </div>
            </div>
        )
    }
}